<template>
  <main class="Produit">
    <div class="cheminSite">
      <router-link to="">Accueil</router-link>
      <span>></span>
      <router-link to="">Produit</router-link>
    </div>

    <div class="produitDetail">
      <div class="pdImagesShow">
        <agile ref="carousel">
           <div  class="pdisItem">
            <img :src="require('@/assets/images/products/'+itemShowed.image_url)" alt="ed" />
          </div>
          
          <div  class="pdisItem">
            <img :src="require('@/assets/images/products/'+itemShowed.variation_0_image)" alt="ed" />
          </div>
          <div  class="pdisItem">
            <img :src="require('@/assets/images/products/'+itemShowed.variation_1_image)" alt="ed" />
          </div>
        </agile>

        <section class="pdisPagination">
          <img :src="require('../assets/images/products/'+itemShowed.image_url)" alt="variant 1"
            class="pdisItem"
            @click="$refs.carousel.goTo(0)"
          />
          <img :src="require('../assets/images/products/'+itemShowed.variation_0_image)" alt="variant 2"
            class="pdisItem"
            @click="$refs.carousel.goTo(1)"
          />
          <img :src="require('../assets/images/products/'+itemShowed.variation_1_image)" alt="variant 3"
            class="pdisItem"
            @click="$refs.carousel.goTo(2)"
          />
        </section>
      </div>
      <div class="pdText">
        <h3>{{itemShowed.category}} </h3>
        <hr />
        <h2>{{itemShowed.name}}</h2>
        <div class="pdtNotesAndPrice">
          <section class="starSection">
            <span class="fa fa-star"></span>
            <span class="fa fa-star"></span>
            <span class="fa fa-star"></span>
            <span class="fa fa-star"></span>
            <span class="fa fa-star"></span>
          </section>

          <h3>{{itemShowed.current_price}} {{itemShowed.currency}}</h3>
        </div>
        <div class="pdtRandomInfo">
          <b>Promotion</b>
          <span>Oui</span>
        </div>
        <div class="pdtRandomInfo">
          <b>Etat</b>
          <span>Oui</span>
        </div>
        <div class="pdtRandomInfo">
          <b>Couleur</b>
          <span>Oui</span>
        </div>

        <div class="pdtSelectQuantityAndPrice">
          <button @click="updateToCart(false,itemShowed.id)" v-if="isOnCart(this.$route.params.id) != false">-</button>
          <button @click="updateItemQuantity(false)" v-else>-</button>
            <span v-if="isOnCart(this.$route.params.id) != false">
              {{isOnCart(this.$route.params.id).quantity}}
            </span>
            <span v-else>
              {{itemQuantity}}
            </span>

          <button @click="updateToCart(true,itemShowed.id)" v-if="isOnCart(this.$route.params.id) != false">+</button>
          <button @click="updateItemQuantity(true)" v-else>+</button>

          <button>0 {{itemShowed.currency}}</button>
        </div>
        <div class="pdtAddToCartOrLike">
            <button v-show="isOnCart(this.$route.params.id) == false"
            @click="toggleToCart(itemShowed.id, itemShowed.name,
             itemShowed.image_url, itemShowed.current_price,itemShowed.currency)">
            Ajouter au panier</button>
            <button><i class="fas fa-heart"></i></button>
        </div>
      </div>
    </div>


    <div class="produitOtherDetail">
      <section class="podTitle">
        <hr>
        <h3 @click="podItem = 1" :class="podItem == 1 ? 'podSelected': ''">Description</h3>
        <h3 @click="podItem = 2" :class="podItem == 2 ? 'podSelected': ''">Autres info</h3>
        <h3  @click="podItem = 4" :class="podItem == 4 ? 'podSelected': ''">Avis clients</h3>
        <hr>
      </section>
      <section class="podText">
        <p v-for="i in podItem" :key="i">Lorem ipsum dolor sit amet consectetur, adipisicing elit. 
          Fuga iusto illum ipsa, perferendis quod ducimus ex, quas velit recusandae distinctio, 
        totam ab. Laboriosam itaque a hic quas deserunt enim beatae.</p>
                </section>
    </div>

    <div class="produitSimilar">
      <h2>Produit Similaire</h2>
      <div class="pdsList">
        <div class="produitCard" v-for="(i,index) in itemSimilar" :key="index">
        <img :src="require('../assets/images/products/'+i.image_url)" alt="Produit Carte">
        <p>{{i.name}}</p>
        <section class="starSection">
            <span class="fa fa-star"></span>
            <span class="fa fa-star"></span>
            <span class="fa fa-star"></span>
            <span class="fa fa-star"></span>
            <span class="fa fa-star"></span>
          </section>
          <h4>{{i.current_price}} {{i.currency}}</h4>

          <section class="pcActions">
            <button>
              <span class="fa fa-eye"></span>
            </button>
            <button>
              <span class="fa fa-heart"></span>
            </button>
            <button>
              <span class="fa fa-shopping-cart"></span>
            </button>
          </section>
      </div>
      </div>
    </div>
  </main>
</template>

<script>
import axios from "axios";
import { VueAgile } from "vue-agile";
import { useStore } from 'vuex';
import {urls} from "../assets/url.json"

export default {
  name: "Produit",
  setup(){
    const store = useStore();
    return {store}
  },
  components: {
    agile: VueAgile,
  },
  data() {
    return {
      baseUrl1: urls[0].baseUrl,
      baseUrl2: urls[1].baseUrl,
      itemShowed: {},
      itemSimilar: [],
      slideItems: [
        { id: 1, name: "1" },
        { id: 2, name: "2" },
        { id: 3, name: "3" },
      ],
      podItem: 1,
      itemQuantity: 1
    };
  },
  methods:{
    isOnCart(itemId){
      let finded = null;
      for (let index = 0; index < this.store.state.cart.length ;index++) {
        if(this.store.state.cart[index].id == itemId - 1){
          finded = this.store.state.cart[index];
          break;
        }
      } 
      return finded != null ? finded : false;
    },
    toggleToCart(id, name, img, price,currency)
        {
            const newAdded = { id: id, name: name,
             image_url: img, currentPrice: price,
             currency: currency, quantity: this.itemQuantity }
            this.store.commit('toggleToCart',newAdded);
        },
    updateToCart(action,itemId){
      this.store.commit('updateToCart',[action,itemId])
    },
    updateItemQuantity(action){
      if(action){
        this.itemQuantity++
      }
      else{
        this.itemQuantity > 1 ? this.itemQuantity-- : '';
      }
    }
  },
  beforeMount(){
    const cid = (this.$route.params.id - 1);
    axios.get(this.baseUrl1+"/Products/GetOne/"+cid)
    .then(response => {
      this.itemShowed = response.data.content[0]
      axios.get(this.baseUrl1+"/Products/Filter/"+response.data.content[0].category+"/aft="+cid+"/lt=4")
      .then(res => this.itemSimilar = res.data.content)
      })
  }
};
</script>

<style scoped>
.Produit {
  /* display: flex; */
}
.Produit img {
  object-fit: cover;
}

.produitDetail {
  width: 95%;
  margin: 20px auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.pdImagesShow {
  display: flex;
  flex-direction: column;
  width: 45%;
}
.pdisItem {
  display: flex;
  justify-content: center;
}
.pdisItem img {
  width: 80%;
  margin: auto !important;
  height: 300px;
}
.pdisPagination {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.pdisPagination img {
  width: 20%;
  height: 100px;
  cursor: pointer;
}

/*  */
h5,h3
{
  color: var(--colorPrincipal);
  margin: 0;
}
hr
{
  width: 20%;
  margin: 0;
}
.pdText {
  width: 45%;
  /* background-color: var(--secondColor); */
}
/*  */
.pdtNotesAndPrice {
  margin: 20px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.pdtNotesAndPrice h3
{
  margin: 0;
}
/*  */
.pdtRandomInfo {
  margin-bottom: 20px;
}
.pdtRandomInfo span {
  margin-left: 30px;
}

/*  */
.pdtSelectQuantityAndPrice button,.pdtAddToCartOrLike button 
{
  padding: 0;
  border-radius: 50px;
  font-size: 15px;
  height: 30px;
}
.pdtSelectQuantityAndPrice
{
  display: flex;
  align-items: center;
  margin: 20px 0;
}
.pdtSelectQuantityAndPrice button
{
  border: solid 2px var(--colorPrincipal);
  color: var(--colorPrincipal);
}
.pdtSelectQuantityAndPrice button:not(:last-child) {
  width: 30px;
}
.pdtSelectQuantityAndPrice button:last-child {
  background-color: var(--pColorD1);
  padding: 0 15px;
  border: none;
  margin-left: 20px;
}

.pdtSelectQuantityAndPrice span {
  margin: 0 10px;
  font-size: 25px;
}
.pdtAddToCartOrLike {
  margin: 20px 0;
}
.pdtAddToCartOrLike button
{
  height: 40px;
}
.pdtAddToCartOrLike button:first-child {
  background-color: var(--colorPrincipal);
  color: white;
  padding: 0 20px;
}
.pdtAddToCartOrLike button:last-child {
  margin-left: 20px;
  width: 40px;
  background-color: var(--pColorD1);
}

/*  */
.produitOtherDetail
{
  width: 95%;
  margin: 50px auto;
}
.podTitle
{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  /* background-color: yellow; */
  align-items: center;
}
.podTitle hr
{
  border-color: var(--colorPrincipal);
}
.podTitle h3
{
  color: #ccc;
  cursor: pointer;
  transition: all 0.3s ease;
}
h3.podSelected
{
  border-radius: 60px;
  padding: 5px 10px;
  border: solid 1px var(--colorPrincipal);
  color: var(--colorPrincipal);
}

/*  */
.produitSimilar
{
  width: 95%;
  margin: auto;
  text-align: center;
}
.pdsList
{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media screen and (max-width: 512px) {
 .produitDetail {
  flex-direction: column;
  justify-content: center;
  }
  .pdImagesShow,.pdText {
  width: 100%;
  }
  .podTitle
  {
    justify-content: center;
  }
  .podTitle hr
  {
    display: none;
  }
  .podTitle h3
  {
    width: 50%;
    text-align: center;
    margin: 10px 0;
  }
  .pdsList
  {
    justify-content: center;
  }
}
</style>
