import { createApp, defineAsyncComponent } from 'vue'

import App from './App.vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import router from './router'
import VueAgile from 'vue-agile'
import { createStore } from 'vuex'

// Create a new store instance.
const store = createStore({
    state() {
        return {
            cart: localStorage.getItem('addedToCart') ? JSON.parse(localStorage.getItem('addedToCart')) : []
        }
    },
    mutations: {
        classToggler(state, className) {
            document.querySelector(className.selected).classList.toggle(className.toggled)
        },
        toggleToCart(state, newAdded) {

            if (state.cart.length > 0) {
                let finded = null;
                for (let i = 0; i < state.cart.length; i++) {
                    if (state.cart[i].id == newAdded.id) {
                        finded = i;
                        break;
                    }
                }

                if (finded != null) {
                    state.cart.splice(finded, 1);
                }
                else {
                    state.cart.push(newAdded)
                }
            }
            else {
                state.cart = [newAdded];
            }
            localStorage.setItem('addedToCart', JSON.stringify(state.cart));

        },
        updateToCart(state, array) {
            for (let index = 0; index < state.cart.length; index++) {
                if (state.cart[index].id === array[1]) {
                    if (array[0] == true) {
                        state.cart[index].quantity++
                    }
                    else {
                        state.cart[index].quantity > 1 ? state.cart[index].quantity-- : '';
                    }
                    return localStorage.setItem('addedToCart', JSON.stringify(state.cart));
                }
            }
        },
        deleteToCart(state, itemId) {
            for (let index = 0; index < state.cart.length; index++) {
                if (state.cart[index].id === itemId) {
                    state.cart.splice(index, 1);
                    // console.log(state.cart);
                    return localStorage.setItem('addedToCart', JSON.stringify(state.cart));
                }
            }
        }
    }
})


const app = createApp(App).use(router);
app.use(store);
app.use(defineAsyncComponent);
app.use(VueAgile);
app.use(VueAxios, axios);
app.mount('#app')
