<template>
    <main class="Magasin">
        <div class="searchPart">
            <input type="search" v-model="searchKey" placeholder="Barre de recherche">
        </div>
        <div class="mobileFilterBtn">
            <button v-on:click="toggleFilterNav()" class="semiRounded">Filtrer</button>
            <!-- <span>{{productFilteredResult.length }} resultats</span> -->
        </div>
        <div class="filterPart">
            <button v-on:click="toggleFilterNav()" class="filterCloser">
                <i class="mdi mdi-close"></i>
            </button>

            <section class="fpCategorys">
                <h2>Categorie</h2>
                <hr>
                <div>
                    <label :for="cf.category" v-for="(cf,index) in categorysFilter" 
                    :key="index" >
                        <input type="radio" :value="cf.category"
                        v-model="selectedCategory"  :id="cf.category" checked
                        v-if="index == 0">
                        <input type="radio" :value="cf.category"
                        v-model="selectedCategory"  :id="cf.category" 
                        v-else>
                        <span>{{cf.category}}</span>
                    </label>
                </div>
            </section>
            <section class="fpPrice">
                <h2>Sous-categorie</h2>
                <hr>
                <div>
                    <label :for="scf.subcategory" v-for="scf in subCategorysFilter" 
                    :key="scf.subcategory">
                    <input type="radio" :value="scf.subcategory"
                    :id="scf.subcategory" v-model="selectedSubCategory">
                    <span >{{scf.subcategory}}</span>
                    </label>
                </div>
            </section>
            <section class="fpPrice">
                <h2>Prix</h2>
                <hr>
                <div>
                    <input type="number" placeholder="Minimum" v-model.number="priceMinFilter" >
                    <input type="number" placeholder="Maximum" v-model.number="priceMaxFilter">
                </div>
            </section>
            <section class="fpState">
                <h2>Etat</h2>
                <hr>
                <label for="isNewOld1">
                    <input type="radio" v-model="isNewOld" 
                    id="isNewOld1" value="Yes">
                    <span> Neuf</span>
                </label>
                
                <label for="isNewOld2">
                    <input type="radio" v-model="isNewOld" 
                    id="isNewOld2" value="No" >
                    <span> Occasion</span>
                </label>

                <label for="isNewOld3">
                    <input type="radio" v-model="isNewOld" 
                    id="isNewOld3" value="Both" >
                    <span> Les deux</span>
                </label>
            </section>
        </div>
        <div class="productResultPart" v-if="productFilteredResult != null">

            <div class="produitCard"  v-for="item in productFilteredResult" :key="item.id">
                <img :src="require('../assets/images/products/'+item.image_url)" alt="Produit Carte">
                <p>{{item.name}}</p>
                <section class="starSection">
                    <span class="fa fa-star"></span>
                    <span class="fa fa-star"></span>
                    <span class="fa fa-star"></span>
                    <span class="fa fa-star"></span>
                    <span class="fa fa-star"></span>
                </section>
                <h4>{{item.current_price}}  {{item.currency}} </h4>

                <section class="pcActions">
                    <router-link :to="'/Produit/'+(parseInt(item.id) + 1)">
                    <span class="mdi mdi-eye"></span>
                    </router-link>
                    <button>
                    <span class="mdi mdi-heart"></span>
                    </button>
                    <button :class="isAddedToCart(item.id)+' btnFcard-'+item.id"
                    v-on:click="toggleToCart(item.id,item.name,
                    item.image_url,item.current_price,item.currency)">
                        <span class="mdi mdi-basket"></span>
                    </button>
                    
                </section>
            </div>
            
        </div>
        <div v-else class="noFound">
                <p>Aucun article trouvé</p>
            </div>
    </main>
</template>
<script>
import '../assets/css/magasin.css';
import axios from 'axios';

import {urls} from '../assets/url.json';

import {useStore} from "vuex";
export default ({
    setup() {
        const store = useStore();
        return {store}
    },
    // { Card },
    data() {
      return{
          baseUrl1: urls[0].baseUrl,
          baseUrl2: urls[1].baseUrl,
          categorysFilter: [],
          selectedCategory : null,
          selectedSubCategory: null,
          subCategorysFilter: null,
          productFilteredResult: null,
          priceMinFilter: null,
          priceMaxFilter: null,
          searchKey: '',
          isNewOld: null,
      }
    },
    methods:
    {
        toggleFilterNav(){
            this.store.commit('classToggler',{selected:'.filterPart',
            toggled:'filterPartVisible'})
        },
        matchedSubCategorys()
        {
            axios.get(this.baseUrl1+"/Products/GetFilterParameter/GetSubCategorys/house")
            .then(response => this.subCategorysFilter = response.data.content)
        },
        handleCategoryFilterChange(){

            axios.get(this.baseUrl1+"/Products/GetFilterParameter/GetSubCategorys/"+this.selectedCategory)
            .then(response => {
                this.selectedSubCategory = null;
                this.subCategorysFilter = response.data.content
                })
        },
        filteredResult(selectedCategory){
            let requestParam = selectedCategory;
            if(this.selectedSubCategory != null)
            {
                requestParam+= '/sc='+this.selectedSubCategory
            }
            if(this.priceMinFilter != null)
            {
                requestParam+= '/prmn='+this.priceMinFilter
            }
            if(this.priceMaxFilter != null)
            {
                requestParam+= '/prmx='+this.priceMaxFilter
            }
            if(this.isNewOld != null)
            {
                requestParam+= '/st='+this.isNewOld
            }
            axios.get(this.baseUrl1+"/Products/Filter/"+requestParam)
            .then(response => this.productFilteredResult = response.data.content )
        },
        searchedResult()
        {
            axios.get(this.baseUrl1+"/Products/Search/key="+this.searchKey)
            .then(response => {
             this.productFilteredResult = response.data.content
             } )
        },
        toggleToCart(id, name, img, price,currency)
        {
            document.querySelector('.btnFcard-'+id).classList.toggle('addedToCart')
            const newAdded = { id: id, name: name,
             image_url: img, currentPrice: price,
             currency: currency, quantity: 1 }
            this.store.commit('toggleToCart',newAdded);
        },
        isAddedToCart(itemId)
        {
            if(localStorage.getItem('addedToCart'))
            {
                const tab = JSON.parse(localStorage.getItem('addedToCart'))
                const result = tab.filter(tab => tab.id == itemId)
                return result.length > 0 ? 'addedToCart' : '';
            }
        }
    },
      mounted()
    {
        let query = "/Products/GetFilterParameter/GetCategorys";

            axios.get(this.baseUrl1+query)
            .then(response => {
                this.categorysFilter = response.data.content;
                this.selectedCategory = response.data.content[0].category;
                this.filteredResult(this.selectedCategory);
            })
        this.matchedSubCategorys();
    },
    watch:
    {
        selectedCategory: function()
        {
            this.handleCategoryFilterChange();
            this.filteredResult(this.selectedCategory)
        },
        priceMinFilter: function()
        {
           this.filteredResult(this.selectedCategory)
        },
        priceMaxFilter: function()
        {
           this.filteredResult(this.selectedCategory)
        },
        searchKey: function()
        {
            this.searchedResult()
        },
        selectedSubCategory :  function()
        {
            this.filteredResult(this.selectedCategory)
        },
        isNewOld: function()
        {
            this.filteredResult(this.selectedCategory)
        },
  
    },
})
</script>
