<template>
  <main class="Pannier" id="pageContent">
    <div class="pTableau">
      <h2>Le Pannier</h2>
      <div class="table">
      <section class="tableTitle">
        <h3>Nom</h3>
        <h3>Prix unitaire</h3>
        <h3>Quantité</h3>
        <h3>Prix total</h3>
      </section>
      <section class="tableRow" v-for="item in getCart" :key="item.id">
        <div class="trName">
          <p>{{item.name}}</p>
          <img :src="require('@/assets/images/products/'+item.image_url)" alt="Image produi" />
        </div>
        <div>
          <p>{{item.currentPrice}} {{item.currency}}</p>
        </div>
        <div class="trUpdater">
          <div>
            <button @click="updateToCart(false,item.id)">-</button>
            <p>{{item.quantity}}</p>
            <button @click="updateToCart(true,item.id)">+</button>
          </div>
          <button @click="deleteToCart(item.id)">X</button>
        </div>
        <div>
          <p>{{Math.round(item.currentPrice * item.quantity)}} {{item.currency}}</p>
        </div>
      </section>
    </div>
    </div>
    <div class="pPrix">
      <h3>Total du pannier</h3>
      <div class="pptBrute">
        <b>Prix Hors Taxe : (HT) </b>
        <span>{{getTotalCartPrice }} USD</span>
      </div>
      <div class="pptLivraison">
        <b>Taxe(TVA) </b>
        <span>0 USD</span>
      </div>
      <div class="pptTotal">
        <b>Total</b>
        <span>{{getTotalCartPrice }} USD</span>
      </div>

      <div class="pptPaiement">
        <button>Proceder au paiement</button>
      </div>
    </div>
  </main>
</template>

<script>
import "../assets/css/cart.css"
import {useStore} from 'vuex';

export default {
  name: 'Pannier',
  setup(){
    const store = useStore();

    return {store};
  },
  methods:{
    updateToCart(action,itemId){
      this.store.commit('updateToCart',[action,itemId])
    },
    deleteToCart(itemId){
      this.store.commit('deleteToCart',itemId)
    }
  },
  computed:{
    getCart(){
      return this.store.state.cart;
    },
    getTotalCartPrice(){
      let total = 0;
      for (let index = 0; index < this.store.state.cart.length; index++) {
        total = total + (this.store.state.cart[index].currentPrice * this.store.state.cart[index].quantity);
        }
        return Math.round(total);
    }
  }
}
</script>