<template>
    <header>
        <nav :style="cssNavVars">
            <div class="navLogo" id="logo">
                <img :src="require('../assets/images/logo.png')" />
            </div>
            <div class="navMobile">
                <i v-on:click="this.store.commit('classToggler',{selected: '.navLinks',toggled:'navLinksVisible'});" class="mdi mdi-menu"></i>
            </div>
            <ul class="navLinks">
                <li><router-link to="/" v-on:click="toggleMobileNav()">
                Accueil</router-link></li>
                <li><router-link to="/Magasin" v-on:click="toggleMobileNav()">
                Magasin</router-link></li>
                <li><router-link to="/Produit/2" v-on:click="toggleMobileNav()">
                Produit</router-link></li>
                <li><router-link to="/Pannier" v-on:click="toggleMobileNav()">
                Pannier
                <span class="navBadge">{{store.state.cart.length}}</span> </router-link></li>
                <li><router-link to="/Paiement">
                Paiement</router-link></li>
            </ul>
        </nav>
    </header>
    
</template>

<script>
import {useStore} from 'vuex';
export default ({
    setup()
    {
        const store = useStore();
        return{store}
    },
    name: 'Navbar',
    methods:{
        
    },
    props:{
        navToggle : {
            type: String},
    },
    computed: {
  }

})
</script>

<style>
header
{
    padding: 0 !important;
    margin: 0 !important;
}
nav
{
    margin: 0;
    padding: 0;
    background-color: var(--cwd1);
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    height: 100%;
    top: 0;
    bottom: 0;
    position: fixed;
    transition: left 0.7s ease;
    z-index: 99999;
    width: 20%;
    left: var(--navToggle);
}
.navMobile
{
    display: none;
}
header #navToogleBg
{
    content: '';
    height: var(--navBgHeight) !important;
    width: var(--navBgWidth);
    opacity: var(--navBgOpacity) ;
    background: rgba(0, 0, 0, 0.5);
    top: 0;
    bottom: 0;
    position: fixed;
    cursor: pointer;
    transition: opacity 2s ease;
    z-index: 99998;
}
.navLogo
{
    width: 60%;
    /* background-color: pink; */
    margin: 30% auto 20% auto;
}
#logo img
{
    height: 100px;
    width: 100%;
}


.navLinks
{
    width: 60%;
    margin: auto;
}
.navLinks a
{
    color: #333;
    transition: color 0.5s ease;
}
.navLinks li
{
    padding: 10px 0;
    margin: 10px 0;
    text-transform: uppercase;
    position: relative;
}
.navLinks li:hover::before
{
    width: 30%;
}
.navLinks li:hover a
{
   color: #ca1515;
}
.navLinks li::before
{
    content: '';
    position: absolute;
    height: 5px;
    width: 0;
    top: 50%;
    left: -40%;
    transform: translateY(-50%);
    background-color: #ca1515;
    transition: all 0.5s ease;
}
.mobileNavbar
{
    display: none;
}
@media screen and (max-width: 512px) {
    nav
    {
        position: inherit;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 20px 10px;
        width: 100%;
    }
    #logo
    {
        width: 100%;
        margin: auto 0;
        /* background-color: olivedrab; */
    }
    .navMobile 
    {
        margin: 0;
        display: block;
       margin-left: auto;
    }
    .navMobile i
    {
        font-size: 25px;
        color: var(--principalColor);
        cursor: pointer;
    }
    .navLinks
    {
        width: 100%;
        max-height: 0;
        opacity: 0;
        transition: all 0.5s;
    }
    .navLinksVisible
    {
        max-height: 400px;
        opacity: 1;
    }
}
</style>
